import { useState, useRef, useCallback, useEffect } from "react";
import { useTranslation } from "react-i18next";
import ReactCrop, { Crop, PixelCrop, centerCrop, makeAspectCrop } from "react-image-crop";
import "react-image-crop/dist/ReactCrop.css";
import {
  Dialog,
  DialogContent,
  DialogDescription,
  DialogFooter,
  DialogHeader,
  DialogTitle,
} from "@/components/ui/dialog";
import { Button } from "@/components/ui/button";
import { Label } from "@/components/ui/label";
import { Badge } from "@/components/ui/badge";
import { Crop as CropIcon, Info, Maximize2 } from "lucide-react";
import { Alert, AlertDescription } from "@/components/ui/alert";

interface ImageCropDialogProps {
  open: boolean;
  onOpenChange: (open: boolean) => void;
  imageFile: File;
  onCropComplete: (croppedFile: File, preview: string) => void;
}

// Recommended dimensions for activity images
const RECOMMENDED_WIDTH = 1200;
const RECOMMENDED_HEIGHT = 630;
const ASPECT_RATIO = RECOMMENDED_WIDTH / RECOMMENDED_HEIGHT;

function centerAspectCrop(
  mediaWidth: number,
  mediaHeight: number,
  aspect: number,
) {
  return centerCrop(
    makeAspectCrop(
      {
        unit: '%',
        width: 90,
      },
      aspect,
      mediaWidth,
      mediaHeight,
    ),
    mediaWidth,
    mediaHeight,
  );
}

export function ImageCropDialog({
  open,
  onOpenChange,
  imageFile,
  onCropComplete,
}: ImageCropDialogProps) {
  const { t } = useTranslation();
  const [imgSrc, setImgSrc] = useState("");
  const [crop, setCrop] = useState<Crop>();
  const [completedCrop, setCompletedCrop] = useState<PixelCrop>();
  const imgRef = useRef<HTMLImageElement>(null);
  const [isCropping, setIsCropping] = useState(false);

  useEffect(() => {
    if (open && imageFile) {
      const reader = new FileReader();
      reader.addEventListener("load", () => {
        setImgSrc(reader.result?.toString() || "");
      });
      reader.readAsDataURL(imageFile);
    }
  }, [open, imageFile]);

  const onImageLoad = useCallback((e: React.SyntheticEvent<HTMLImageElement>) => {
    const { width, height } = e.currentTarget;
    setCrop(centerAspectCrop(width, height, ASPECT_RATIO));
  }, []);

  const getCroppedImg = useCallback(
    async (image: HTMLImageElement, crop: PixelCrop): Promise<Blob> => {
      const canvas = document.createElement("canvas");
      const ctx = canvas.getContext("2d");

      if (!ctx) {
        throw new Error("No 2d context");
      }

      const scaleX = image.naturalWidth / image.width;
      const scaleY = image.naturalHeight / image.height;

      // Set canvas size to match the crop area
      canvas.width = crop.width * scaleX;
      canvas.height = crop.height * scaleY;

      ctx.imageSmoothingQuality = "high";

      ctx.drawImage(
        image,
        crop.x * scaleX,
        crop.y * scaleY,
        crop.width * scaleX,
        crop.height * scaleY,
        0,
        0,
        canvas.width,
        canvas.height
      );

      return new Promise((resolve, reject) => {
        canvas.toBlob(
          (blob) => {
            if (!blob) {
              reject(new Error("Canvas is empty"));
              return;
            }
            resolve(blob);
          },
          "image/jpeg",
          0.95
        );
      });
    },
    []
  );

  const handleCropConfirm = useCallback(async () => {
    if (!completedCrop || !imgRef.current) return;

    setIsCropping(true);
    try {
      const croppedBlob = await getCroppedImg(imgRef.current, completedCrop);
      const croppedFile = new File([croppedBlob], imageFile.name, {
        type: "image/jpeg",
      });

      // Create preview URL
      const previewUrl = URL.createObjectURL(croppedBlob);

      onCropComplete(croppedFile, previewUrl);
      onOpenChange(false);
    } catch (error) {
      console.error("Error cropping image:", error);
    } finally {
      setIsCropping(false);
    }
  }, [completedCrop, imageFile, onCropComplete, onOpenChange, getCroppedImg]);

  return (
    <Dialog open={open} onOpenChange={onOpenChange}>
      <DialogContent className="max-w-[95vw] sm:max-w-4xl max-h-[95vh] overflow-hidden flex flex-col p-0">
        <DialogHeader className="px-4 sm:px-6 pt-4 sm:pt-6 pb-4 border-b">
          <DialogTitle className="text-lg sm:text-xl flex items-center gap-2">
            <CropIcon className="w-5 h-5 text-primary" />
            {t("admin.cropImage")}
          </DialogTitle>
          <DialogDescription className="text-xs sm:text-sm">
            {t("admin.cropImageDescription")}
          </DialogDescription>
        </DialogHeader>

        <div className="px-4 sm:px-6 py-4 space-y-4 overflow-y-auto">
          <Alert className="border-primary/20 bg-primary/5">
            <Info className="h-4 w-4" />
            <AlertDescription className="text-xs sm:text-sm">
              <div className="flex flex-col sm:flex-row sm:items-center gap-2">
                <span className="font-medium">{t("admin.recommendedSize")}:</span>
                <Badge variant="secondary" className="w-fit">
                  <Maximize2 className="w-3 h-3 mr-1" />
                  {RECOMMENDED_WIDTH} √ó {RECOMMENDED_HEIGHT} px
                </Badge>
              </div>
              <p className="mt-2 text-muted-foreground text-xs">
                {t("admin.imageSizeHint")}
              </p>
            </AlertDescription>
          </Alert>

          <div className="relative w-full overflow-hidden rounded-lg border">
            {imgSrc && (
              <ReactCrop
                crop={crop}
                onChange={(c) => setCrop(c)}
                onComplete={(c) => setCompletedCrop(c)}
                aspect={ASPECT_RATIO}
                className="max-h-[50vh] sm:max-h-[60vh]"
              >
                <img
                  ref={imgRef}
                  src={imgSrc}
                  alt="Crop preview"
                  onLoad={onImageLoad}
                  className="max-w-full h-auto"
                />
              </ReactCrop>
            )}
          </div>
        </div>

        <DialogFooter className="px-4 sm:px-6 pb-4 sm:pb-6 pt-4 border-t gap-2 sm:gap-0">
          <Button
            type="button"
            variant="outline"
            onClick={() => onOpenChange(false)}
            disabled={isCropping}
            className="w-full sm:w-auto"
          >
            {t("common.cancel")}
          </Button>
          <Button
            type="button"
            onClick={handleCropConfirm}
            disabled={!completedCrop || isCropping}
            className="w-full sm:w-auto"
          >
            {isCropping ? t("common.processing") : t("admin.confirmCrop")}
          </Button>
        </DialogFooter>
      </DialogContent>
    </Dialog>
  );
}
