import { useState, useEffect } from "react";
import { Dialog, DialogContent, DialogHeader, DialogTitle, DialogTrigger, DialogFooter } from "@/components/ui/dialog";
import { Button } from "@/components/ui/button";
import { Tabs, TabsContent, TabsList, TabsTrigger } from "@/components/ui/tabs";
import { useActivityRules, useUpdateActivityRules } from "@/hooks/useActivityRules";
import { useTranslation } from "react-i18next";
import { Loader2, FileText, Save } from "lucide-react";
import { toast } from "@/hooks/use-toast";
import { RichTextEditor } from "./RichTextEditor";

interface ActivityRulesEditorDialogProps {
  children: React.ReactNode;
}

const ActivityRulesEditorDialog = ({ children }: ActivityRulesEditorDialogProps) => {
  const { t } = useTranslation();
  const { data: rules, isLoading } = useActivityRules();
  const updateRules = useUpdateActivityRules();
  const [open, setOpen] = useState(false);
  const [activeTab, setActiveTab] = useState("zh");
  
  const [contentZh, setContentZh] = useState("");
  const [contentEn, setContentEn] = useState("");
  const [contentPt, setContentPt] = useState("");

  useEffect(() => {
    if (rules) {
      setContentZh(rules.content_zh);
      setContentEn(rules.content_en);
      setContentPt(rules.content_pt);
    }
  }, [rules]);

  const handleSave = async () => {
    if (!rules) return;
    
    try {
      await updateRules.mutateAsync({
        id: rules.id,
        content_zh: contentZh,
        content_en: contentEn,
        content_pt: contentPt,
      });
      toast({
        title: t("common.success"),
        description: "Ê¥ªÂä®Êù°‰æãÂ∑≤Êõ¥Êñ∞",
      });
      setOpen(false);
    } catch (error) {
      toast({
        title: t("common.error"),
        description: "Êõ¥Êñ∞Â§±Ë¥•",
        variant: "destructive",
      });
    }
  };

  return (
    <Dialog open={open} onOpenChange={setOpen}>
      <DialogTrigger asChild>
        {children}
      </DialogTrigger>
      <DialogContent className="max-w-4xl max-h-[90vh] overflow-y-auto">
        <DialogHeader>
          <DialogTitle className="flex items-center gap-2">
            <FileText className="w-5 h-5" />
            ÁºñËæëÊ¥ªÂä®Êù°‰æã
          </DialogTitle>
        </DialogHeader>
        
        {isLoading ? (
          <div className="flex justify-center py-8">
            <Loader2 className="w-6 h-6 animate-spin text-primary" />
          </div>
        ) : (
          <div className="space-y-4">
            <Tabs value={activeTab} onValueChange={setActiveTab}>
              <TabsList className="grid w-full grid-cols-3">
                <TabsTrigger value="zh">‰∏≠Êñá</TabsTrigger>
                <TabsTrigger value="en">English</TabsTrigger>
                <TabsTrigger value="pt">Portugu√™s</TabsTrigger>
              </TabsList>
              
              <TabsContent value="zh" className="mt-4">
                <RichTextEditor
                  value={contentZh}
                  onChange={setContentZh}
                  placeholder="ËØ∑ËæìÂÖ•‰∏≠ÊñáÊ¥ªÂä®Êù°‰æãÂÜÖÂÆπ..."
                />
              </TabsContent>
              
              <TabsContent value="en" className="mt-4">
                <RichTextEditor
                  value={contentEn}
                  onChange={setContentEn}
                  placeholder="Enter English activity rules..."
                />
              </TabsContent>
              
              <TabsContent value="pt" className="mt-4">
                <RichTextEditor
                  value={contentPt}
                  onChange={setContentPt}
                  placeholder="Digite as regras da atividade em portugu√™s..."
                />
              </TabsContent>
            </Tabs>
          </div>
        )}
        
        <DialogFooter>
          <Button variant="outline" onClick={() => setOpen(false)}>
            {t("common.cancel")}
          </Button>
          <Button onClick={handleSave} disabled={updateRules.isPending}>
            {updateRules.isPending ? (
              <Loader2 className="w-4 h-4 mr-2 animate-spin" />
            ) : (
              <Save className="w-4 h-4 mr-2" />
            )}
            {t("common.save")}
          </Button>
        </DialogFooter>
      </DialogContent>
    </Dialog>
  );
};

export default ActivityRulesEditorDialog;
