import { useState, useMemo } from "react";
import { Link } from "react-router-dom";
import { useTranslation } from "react-i18next";
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from "@/components/ui/card";
import { Button } from "@/components/ui/button";
import { Badge } from "@/components/ui/badge";
import { Checkbox } from "@/components/ui/checkbox";
import { Input } from "@/components/ui/input";
import {
  Select,
  SelectContent,
  SelectItem,
  SelectTrigger,
  SelectValue,
} from "@/components/ui/select";
import {
  AlertDialog,
  AlertDialogAction,
  AlertDialogCancel,
  AlertDialogContent,
  AlertDialogDescription,
  AlertDialogFooter,
  AlertDialogHeader,
  AlertDialogTitle,
  AlertDialogTrigger,
} from "@/components/ui/alert-dialog";
import { useAdminActivities } from "@/hooks/useActivities";
import { useLocalizedContent } from "@/hooks/useLocalizedContent";
import { useDeleteActivity, useBatchDeleteActivities } from "@/hooks/useAdmin";

import { ActivityFormDialog } from "@/components/admin/ActivityFormDialog";
import ActivityRulesEditorDialog from "@/components/admin/ActivityRulesEditorDialog";
import GlobalHandbookEditorDialog from "@/components/admin/GlobalHandbookEditorDialog";
import { Calendar, Plus, Download, Trash2, Search, FileText, FileEdit, Send, BarChart3, BookOpen } from "lucide-react";
import { format } from "date-fns";
import { exportActivitiesToCSV } from "@/lib/exportUtils";
import { useToast } from "@/hooks/use-toast";
const AdminActivities = () => {
  const { t } = useTranslation();
  const { toast } = useToast();
  const { data: activities, isLoading } = useAdminActivities();
  const { getLocalizedField } = useLocalizedContent();
  const deleteActivity = useDeleteActivity();
  const batchDeleteActivities = useBatchDeleteActivities();
  const [selectedActivities, setSelectedActivities] = useState<string[]>([]);
  const [searchQuery, setSearchQuery] = useState("");
  const [statusFilter, setStatusFilter] = useState<string>("all");
  const [deleteConfirmText, setDeleteConfirmText] = useState<{ [key: string]: string }>({});
  const [batchDeleteConfirmText, setBatchDeleteConfirmText] = useState("");

  // Filter activities based on search query and status filter
  const filteredActivities = useMemo(() => {
    if (!activities) return [];
    
    return activities.filter((activity) => {
      const title = String(getLocalizedField(activity, "title") || "");
      const content = String(getLocalizedField(activity, "content") || "");
      const matchesSearch = searchQuery === "" || 
        title.toLowerCase().includes(searchQuery.toLowerCase()) ||
        content.toLowerCase().includes(searchQuery.toLowerCase());
      
      const now = new Date();
      const startDate = new Date(activity.start_date);
      // If no end_date, activity is ongoing indefinitely
      const endDate = activity.end_date ? new Date(activity.end_date) : null;
      const isEnded = endDate ? endDate < now : false;
      const isOngoing = !isEnded && startDate <= now;
      
      const activityStatus = (activity as any).status || "published";
      const matchesStatus = 
        statusFilter === "all" ||
        (statusFilter === "active" && isOngoing) ||
        (statusFilter === "ended" && isEnded) ||
        (statusFilter === "pinned" && activity.pinned) ||
        (statusFilter === "draft" && activityStatus === "draft") ||
        (statusFilter === "published" && activityStatus === "published");
      
      return matchesSearch && matchesStatus;
    });
  }, [activities, searchQuery, statusFilter, getLocalizedField]);

  const handleExport = () => {
    if (!activities || activities.length === 0) {
      toast({
        title: t("common.error"),
        description: t("admin.noDataToExport"),
        variant: "destructive",
      });
      return;
    }

    exportActivitiesToCSV(activities);
    toast({
      title: t("common.success"),
      description: t("admin.exportSuccess"),
    });
  };

  const toggleSelectActivity = (activityId: string) => {
    setSelectedActivities(prev =>
      prev.includes(activityId)
        ? prev.filter(id => id !== activityId)
        : [...prev, activityId]
    );
  };

  const toggleSelectAll = () => {
    if (selectedActivities.length === filteredActivities?.length) {
      setSelectedActivities([]);
    } else {
      setSelectedActivities(filteredActivities?.map(a => a.id) || []);
    }
  };

  const handleBatchDelete = () => {
    if (selectedActivities.length === 0) return;
    batchDeleteActivities.mutate(selectedActivities);
    setSelectedActivities([]);
  };

  if (isLoading) {
    return <div className="text-center">{t("common.loading")}</div>;
  }

  return (
    <div className="space-y-6">
        <div className="flex flex-col md:flex-row md:items-center justify-between gap-4">
          <div>
            <h1 className="text-2xl md:text-3xl font-bold mb-2">{t("admin.activityManagement")}</h1>
            <p className="text-muted-foreground">{t("admin.manageActivities")}</p>
          </div>
          <div className="flex gap-2 flex-wrap">
            <GlobalHandbookEditorDialog>
              <Button variant="outline" className="flex-1 sm:flex-initial">
                <BookOpen className="mr-2 h-4 w-4" />
                <span className="hidden sm:inline">{t("admin.activityHandbook")}</span>
              </Button>
            </GlobalHandbookEditorDialog>
            <ActivityRulesEditorDialog>
              <Button variant="outline" className="flex-1 sm:flex-initial">
                <FileText className="mr-2 h-4 w-4" />
                <span className="hidden sm:inline">{t("admin.activityRules")}</span>
              </Button>
            </ActivityRulesEditorDialog>
            <Button variant="outline" onClick={handleExport} className="flex-1 sm:flex-initial">
              <Download className="mr-2 h-4 w-4" />
              <span className="hidden sm:inline">{t("admin.exportCSV")}</span>
            </Button>
            <ActivityFormDialog
              trigger={
                <Button className="flex-1 sm:flex-initial">
                  <Plus className="mr-2 h-4 w-4" />
                  {t("admin.addActivity")}
                </Button>
              }
            />
          </div>
        </div>

        {/* Search and Filter */}
        <Card>
          <CardContent className="pt-6">
            <div className="flex gap-4 flex-wrap">
              <div className="flex-1 min-w-[200px]">
                <div className="relative">
                  <Search className="absolute left-3 top-1/2 transform -translate-y-1/2 h-4 w-4 text-muted-foreground" />
                  <Input
                    placeholder={t("admin.searchActivities")}
                    value={searchQuery}
                    onChange={(e) => setSearchQuery(e.target.value)}
                    className="pl-9"
                  />
                </div>
              </div>
              <Select value={statusFilter} onValueChange={setStatusFilter}>
                <SelectTrigger className="w-[180px]">
                  <SelectValue placeholder={t("admin.filterByStatus")} />
                </SelectTrigger>
                <SelectContent>
                  <SelectItem value="all">{t("admin.allStatus")}</SelectItem>
                  <SelectItem value="draft">{t("admin.statusDraft")}</SelectItem>
                  <SelectItem value="published">{t("admin.statusPublished")}</SelectItem>
                  <SelectItem value="active">{t("admin.statusActive")}</SelectItem>
                  <SelectItem value="ended">{t("admin.statusEnded")}</SelectItem>
                  <SelectItem value="pinned">{t("admin.statusPinned")}</SelectItem>
                </SelectContent>
              </Select>
            </div>
          </CardContent>
        </Card>

        {/* Batch Operations */}
        {selectedActivities.length > 0 && (
          <Card className="bg-muted/50">
            <CardContent className="pt-6">
              <div className="flex flex-col sm:flex-row items-start sm:items-center gap-4">
                <span className="font-medium">
                  {t("admin.selectedCount", { count: selectedActivities.length })}
                </span>

                <div className="flex gap-2 flex-wrap w-full sm:w-auto">
                  <AlertDialog>
                    <AlertDialogTrigger asChild>
                      <Button variant="destructive" size="sm" className="flex-1 sm:flex-initial">
                        <Trash2 className="mr-2 h-4 w-4" />
                        {t("admin.batchDelete")}
                      </Button>
                    </AlertDialogTrigger>
                    <AlertDialogContent className="max-w-[90vw] sm:max-w-[425px]">
                      <AlertDialogHeader>
                        <AlertDialogTitle>{t("admin.confirmBatchDelete")}</AlertDialogTitle>
                        <AlertDialogDescription className="space-y-3">
                          <p>{t("admin.batchDeleteWarning", { count: selectedActivities.length })}</p>
                          <p className="font-semibold text-destructive">
                            {t("admin.willDeleteActivities", { count: selectedActivities.length })}
                          </p>
                          <div className="space-y-2">
                            <p className="text-sm">{t("admin.typeDeleteToConfirmBatch")}</p>
                            <Input
                              value={batchDeleteConfirmText}
                              onChange={(e) => setBatchDeleteConfirmText(e.target.value)}
                              placeholder="DELETE"
                              className="font-mono"
                            />
                          </div>
                        </AlertDialogDescription>
                      </AlertDialogHeader>
                      <AlertDialogFooter>
                        <AlertDialogCancel onClick={() => setBatchDeleteConfirmText("")}>
                          {t("common.cancel")}
                        </AlertDialogCancel>
                        <AlertDialogAction
                          disabled={batchDeleteConfirmText !== "DELETE"}
                          onClick={() => {
                            handleBatchDelete();
                            setBatchDeleteConfirmText("");
                          }}
                          className="bg-destructive text-destructive-foreground hover:bg-destructive/90"
                        >
                          {t("common.delete")}
                        </AlertDialogAction>
                      </AlertDialogFooter>
                    </AlertDialogContent>
                  </AlertDialog>

                  <Button variant="outline" size="sm" onClick={() => setSelectedActivities([])} className="flex-1 sm:flex-initial">
                    {t("admin.clearSelection")}
                  </Button>
                </div>
              </div>
            </CardContent>
          </Card>
        )}

        {/* Select All */}
        <div className="flex items-center gap-2 pb-2 border-b">
          <Checkbox
            checked={selectedActivities.length === filteredActivities?.length && filteredActivities.length > 0}
            onCheckedChange={toggleSelectAll}
          />
          <span className="font-medium">{t("admin.selectAll")}</span>
          <span className="text-sm text-muted-foreground ml-2">
            ({filteredActivities?.length} {t("admin.results")})
          </span>
        </div>

        <div className="grid gap-4">
          {filteredActivities?.map((activity) => (
            <Card key={activity.id}>
              <div className="flex flex-col md:flex-row md:items-center">
                <div className="p-4 flex items-start md:self-start">
                  <Checkbox
                    checked={selectedActivities.includes(activity.id)}
                    onCheckedChange={() => toggleSelectActivity(activity.id)}
                    className="mt-1"
                  />
                </div>
                {activity.image_url && (
                  <div className="w-full md:w-48 h-48 flex items-center justify-center overflow-hidden mx-auto md:mx-0 flex-shrink-0">
                    <img
                      src={activity.image_url}
                      alt={String(getLocalizedField(activity, "title"))}
                      className="object-cover w-full h-full"
                    />
                  </div>
                )}
                <div className="flex-1">
                  <CardHeader>
                    <div className="flex flex-col sm:flex-row sm:items-center justify-between gap-2">
                      <CardTitle className="text-lg md:text-xl">{getLocalizedField(activity, "title")}</CardTitle>
                      <div className="flex gap-2 flex-wrap">
                        {(activity as any).status === "draft" ? (
                          <Badge variant="outline" className="bg-yellow-500/10 text-yellow-600 border-yellow-500/30">
                            <FileEdit className="w-3 h-3 mr-1" />
                            ËçâÁ®ø
                          </Badge>
                        ) : (
                          <Badge variant="outline" className="bg-green-500/10 text-green-600 border-green-500/30">
                            <Send className="w-3 h-3 mr-1" />
                            Â∑≤ÂèëÂ∏É
                          </Badge>
                        )}
                        {!activity.visible && <Badge variant="secondary">{t("admin.hidden")}</Badge>}
                        {activity.pinned && <Badge>{t("activities.pinned")}</Badge>}
                        {(() => {
                          const now = new Date();
                          const startDate = new Date(activity.start_date);
                          const endDate = activity.end_date ? new Date(activity.end_date) : null;
                          const isEnded = endDate ? endDate < now : false;
                          const isOngoing = !isEnded && startDate <= now;
                          
                          if (isEnded) {
                            return <Badge variant="secondary">{t("activities.ended")}</Badge>;
                          } else if (isOngoing) {
                            return <Badge>{t("activities.ongoing")}</Badge>;
                          }
                          return null;
                        })()}
                      </div>
                    </div>
                    <CardDescription className="line-clamp-2">
                      <div 
                        className="prose prose-sm max-w-none
                          [&_p]:inline [&_p]:m-0
                          [&_strong]:font-semibold [&_strong]:text-foreground
                          [&_em]:italic
                          [&_h1]:text-base [&_h2]:text-base [&_h3]:text-base
                          [&_*]:text-muted-foreground"
                        dangerouslySetInnerHTML={{ __html: getLocalizedField(activity, "content") }}
                      />
                    </CardDescription>
                  </CardHeader>
                  <CardContent>
                    <div className="flex items-center gap-4 text-sm text-muted-foreground mb-4 overflow-x-auto">
                      <div className="flex items-center gap-1 whitespace-nowrap">
                        <Calendar className="h-4 w-4 flex-shrink-0" />
                        <span className="text-xs sm:text-sm">
                          {format(new Date(activity.start_date), "PPP")}
                          {activity.end_date &&
                            ` - ${format(new Date(activity.end_date), "PPP")}`}
                        </span>
                      </div>
                    </div>
                    <div className="flex gap-2 flex-wrap">
                      {(activity as any).status !== "draft" && (
                        <Button variant="outline" size="sm" asChild className="flex-1 sm:flex-initial">
                          <Link to={`/admin/activity-stats/${activity.id}`}>
                            <BarChart3 className="w-4 h-4 mr-1" />
                            {t("admin.stats", "ÁªüËÆ°")}
                          </Link>
                        </Button>
                      )}
                      <Button variant="outline" size="sm" asChild className="flex-1 sm:flex-initial">
                        <Link to={`/activity/${activity.id}`} state={{ from: "admin" }}>{t("admin.view")}</Link>
                      </Button>
                      <ActivityFormDialog
                        activity={activity}
                        trigger={
                          <Button variant="outline" size="sm" className="flex-1 sm:flex-initial">
                            {t("common.edit")}
                          </Button>
                        }
                      />
                      {(activity as any).status === "draft" ? (
                        /* Draft activity: simple confirmation */
                        <AlertDialog>
                          <AlertDialogTrigger asChild>
                            <Button variant="outline" size="sm" className="flex-1 sm:flex-initial">
                              {t("common.delete")}
                            </Button>
                          </AlertDialogTrigger>
                          <AlertDialogContent className="max-w-[90vw] sm:max-w-[425px]">
                            <AlertDialogHeader>
                              <AlertDialogTitle>{t("admin.confirmDelete")}</AlertDialogTitle>
                              <AlertDialogDescription>
                                <p>{t("admin.deleteActivityWarning")}</p>
                                <p className="font-semibold text-foreground mt-2">
                                  {getLocalizedField(activity, "title")}
                                </p>
                              </AlertDialogDescription>
                            </AlertDialogHeader>
                            <AlertDialogFooter>
                              <AlertDialogCancel>{t("common.cancel")}</AlertDialogCancel>
                              <AlertDialogAction
                                onClick={() => deleteActivity.mutate(activity.id)}
                                className="bg-destructive text-destructive-foreground hover:bg-destructive/90"
                              >
                                {t("common.delete")}
                              </AlertDialogAction>
                            </AlertDialogFooter>
                          </AlertDialogContent>
                        </AlertDialog>
                      ) : (
                        /* Published activity: strict verification */
                        <AlertDialog>
                          <AlertDialogTrigger asChild>
                            <Button variant="outline" size="sm" className="flex-1 sm:flex-initial">
                              {t("common.delete")}
                            </Button>
                          </AlertDialogTrigger>
                          <AlertDialogContent className="max-w-[90vw] sm:max-w-[425px]">
                            <AlertDialogHeader>
                              <AlertDialogTitle>{t("admin.confirmDelete")}</AlertDialogTitle>
                              <AlertDialogDescription className="space-y-3">
                                <p>{t("admin.deleteActivityWarning")}</p>
                                <p className="font-semibold text-foreground">
                                  {getLocalizedField(activity, "title")}
                                </p>
                                <div className="space-y-2">
                                  <p className="text-sm">{t("admin.typeDeleteToConfirmBatch")}</p>
                                  <Input
                                    value={deleteConfirmText[activity.id] || ""}
                                    onChange={(e) => setDeleteConfirmText({ ...deleteConfirmText, [activity.id]: e.target.value })}
                                    placeholder="DELETE"
                                    className="font-mono"
                                  />
                                </div>
                              </AlertDialogDescription>
                            </AlertDialogHeader>
                            <AlertDialogFooter>
                              <AlertDialogCancel onClick={() => setDeleteConfirmText({ ...deleteConfirmText, [activity.id]: "" })}>
                                {t("common.cancel")}
                              </AlertDialogCancel>
                              <AlertDialogAction
                                disabled={deleteConfirmText[activity.id] !== "DELETE"}
                                onClick={() => {
                                  deleteActivity.mutate(activity.id);
                                  setDeleteConfirmText({ ...deleteConfirmText, [activity.id]: "" });
                                }}
                                className="bg-destructive text-destructive-foreground hover:bg-destructive/90"
                              >
                                {t("common.delete")}
                              </AlertDialogAction>
                            </AlertDialogFooter>
                          </AlertDialogContent>
                        </AlertDialog>
                      )}
                    </div>
                  </CardContent>
                </div>
              </div>
            </Card>
          ))}
        </div>
      </div>
    );
  };

  export default AdminActivities;
